.popup_infotooltip {
  visibility: hidden;
  position: fixed;
  opacity: 0;
  transition: visibility 0.2s linear, opacity 0.2s linear;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 0, 0, 0.5);
}
