.header {
    max-width: 880px;
    margin: 0 auto;
    padding-top: 45px;
    border-bottom: 1px solid rgba(54, 54, 54, .7);
    display: flex;
    justify-content: space-between;
}

@media all and (max-width: 928px) {
    .header {
        padding-top: 28px;
        padding-left: 27px;
    }
}